@charset "UTF-8";
/**
 * z-index list of importance
 * 10000 - Laravel Debug Bar. NEVER SURPASS.
 *  9999 - jQuery Blocker Overlay.
 *  1100 - Hover-over posts.
 *  1000 - Boardlist, top. Should be highest element on boards and threads.
 *  1000 - Post form.
 *    10 - Post action links.
 *     5 - Post attachment.
 *     1 - Global Footer
 *     0 - Board Sidebar
 */
/**
 * Color references:
 *
 * Larachan
 * #f4645f - Salmon Pink
 *
 * Burichan
 * #EEF2FF - Background
 * #d6daf0 - Medium blue
