html,
body {
  background-color: #EEF2FF;
  background-image: -ms-linear-gradient(top, #d1d5ee 0%, #d1d5ee 40px, #EEF2FF 240px, #EEF2FF 100%);
  background-image: -moz-linear-gradient(top, #d1d5ee 0%, #d1d5ee 40px, #EEF2FF 240px, #EEF2FF 100%);
  background-image: -o-linear-gradient(top, #d1d5ee 0%, #d1d5ee 40px, #EEF2FF 240px, #EEF2FF 100%);
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(0, #d1d5ee), color-stop(40px, #d1d5ee), color-stop(240px, #EEF2FF), color-stop(100, #EEF2FF));
  background-image: -webkit-linear-gradient(top, #d1d5ee 0%, #d1d5ee 40px, #EEF2FF 240px, #EEF2FF 100%);
  background-image: linear-gradient(to bottom, #d1d5ee 0%, #d1d5ee 40px, #EEF2FF 240px, #EEF2FF 100%);
  box-sizing: border-box;
  min-width: 300px;
  min-height: 100vh;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 12px;
  color: #111111;
  -webkit-text-size-adjust: 100%; }

body {
  display: flex;
  flex-direction: column;
  min-width: 340px; }

#page-container {
  position: relative;
  flex: 1;
  /*
		Microsoft is a piece of shit and I hope they all get ass cancer.
		For whatever reason, flex items in MS11 do not have a height.
		Instead, the main body gets a height of 0, and the footer appears
		right below the header.

		I've tried many other alternatives to this but there doesn't seem
		to be a way to have a flexible height sticky footer.
		The only solution is to use the easy layout for all modern browsers
		and let IE degrade gracefully, with the footer appearing where it
		would be without Flexbox.
	*/
  -ms-flex: none; }

a,
