/*
 * Generic Classes
 */
.ugc {
  /*
		DO NOT PUT THIS HERE
		It stops post bodies from wrapping their images!

		overflow: hidden;
	*/ }

/* Function */
.selfclear::after {
  display: block;
  content: ' ';
